import Mock from 'mockjs';
const LoginUsers = [
  {
    id: 1,
    username: 'admin',
    password: '123456',
    avatar: 'https://avatars3.githubusercontent.com/u/14358159?v=3&s=460',
    name: 'CH'
  }
];

const Users = [];

for (let i = 0; i < 86; i++) {
  Users.push(Mock.mock({
    id: Mock.Random.guid(),
    name: Mock.Random.cname(),
    addr: Mock.mock('@county(true)'),
    stat: Mock.Random.pick(['', '']),
    type: Mock.Random.pick(['', '','']),
    phone:Mock.Random.pick(['18651908626', '18522861748', '18139500119', '17717311131', '1791651515']),
    time: Mock.Random.pick(['2017.05.16 1805', '2017.05.17 1400', '2017.05.16 1200', '2017.05.15 1100', '2017.05.14 1000']),
    store: Mock.Random.pick(['', '', '', '', '']),
    'age|18-60': 1,
    star: Mock.Random.pick(['', '','','','']),
    birth: Mock.Random.date(),
    sex: Mock.Random.integer(0, 1)
  }));
}

export { LoginUsers, Users };



